<template>
  <div class="container">
    <h1>Böcker</h1>
    <p class="error" v-if="error">{{error}}</p>

    <div class="add-book">
      <label for="title">Titel</label><br/>
      <input type="text" id="title" v-model="title" placeholder="Titel" required/><br/>
      <label for="author">Författare</label><br/>
      <input type="text" id="author" v-model="author" placeholder="Författare" required/><br/>
      <label for="published">Publiceringsår</label><br/>
      <input type="text" id="published" v-model="published" placeholder="Publiceringsår" required/><br/>
      <label for="add-book">Antal sidor</label><br/>
      <input type="text" id="pages" v-model="pages" placeholder="Antal sidor" required/><br/>
      <button v-on:click="addBook">Lägg till bok</button>
    </div>

    <div class="books-container">
      <div
        class="book"
        v-for="(book, index) in books"
        v-bind:item="book"
        v-bind:index="index"
        v-bind:key="book._id"
        v-on:dblclick="deleteBook(book._id)"
      >
        <p class="title">Titel: {{book.title}}</p>
        <p class="author">Författare: {{book.author}}</p>
        <p class="published">Publiceringsår: {{book.published}}</p>
        <p class="pages">Antal sidor: {{book.pages}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import BookService from "../BookService";

export default {
  name: "BookComponent",
  data() {
    return {
      books: [],
      error: "",
      title: "",
      author: "",
      published: "",
      pages: ""
    };
  },
  async created() {
    try {
      this.books = await BookService.getBooks();
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    async addBook() {
      await BookService.addBook(
        this.title,
        this.author,
        this.published,
        this.pages
      );
      this.books = await BookService.getBooks();
    },
    async deleteBook(id) {
      await BookService.deleteBook(id);
      this.books = await BookService.getBooks();
    }
  }
};
</script>

